const utils = {
    noop() {
    },

    /**
     * Slightly edited polyfill from MDN
     */
    assign(target, varArgs) { // .length of function is 2
        if (target == null) { // TypeError if undefined or null
            throw new TypeError('Cannot convert undefined or null to object');
        }

        const to = Object(target);
        for (let index = 1; index < arguments.length; index++) {
            const nextSource = arguments[index];

            if (nextSource != null) { // Skip over if undefined or null
                for (let nextKey in nextSource) {
                    // Avoid bugs when hasOwnProperty is shadowed
                    if (Object.prototype.hasOwnProperty.call(nextSource, nextKey)) {
                        to[nextKey] = nextSource[nextKey];
                    }
                }
            }
        }
        return to;
    },

    parseTime(timeString) {
        const msIndex = timeString.indexOf('.');
        if (msIndex > 0) {
            timeString = timeString.slice(0, msIndex);
        }

        const ar = timeString.split(':');
        let result = 0;
        if (ar.length === 3) {
            result = parseInt(ar[2]) + parseInt(ar[1]) * 60 + parseInt(ar[0]) * 3600;
        } else if (ar.length === 2) {
            result = parseInt(ar[1]) + parseInt(ar[0]) * 60;
        } else {
            return -1;
        }
        return result;
    },

    formatTime(seconds) {
        // handle invalid times
        if (seconds == null || seconds < 0 || isNaN(seconds) || seconds === Infinity) {
            return '-';
        }

        let s = Math.floor(seconds % 60),
            m = Math.floor(seconds / 60 % 60),
            h = Math.floor(seconds / 3600);

        h = h > 0 ? h + ':' : '';
        m = (m < 10 ? '0' + m : m) + ':';
        s = (s < 10) ? '0' + s : s;

        return h + m + s;
    },

    getUrlContentAsync(url, callback) {
        if (location.protocol === 'https:' && url.toString().substr(0, 5) === 'http:') {
            console.error('Incorrect URL protocol. Expected to be HTTPS request');
            return callback();
        }

        try {
            const xhr = new XMLHttpRequest();
            xhr.open('GET', url, true);

            xhr.onload = function () {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        callback(xhr.responseText);
                    } else {
                        callback();
                    }
                }
            };

            xhr.onerror = function (e) {
                console.error(e.message);
                callback();
            };

            xhr.send();
        } catch (e) {
            console.error(e.message);
            callback();
        }
    },

    parseXML(xmlString) {
        const parser = new DOMParser();
        return parser.parseFromString(xmlString, 'text/xml');
    },

    getCDATA(xmlNode) {
        if (xmlNode.firstChild) {
            if (xmlNode.firstChild.wholeText) {
                return xmlNode.firstChild.wholeText.trim();
            }
            return xmlNode.firstChild.data;
        }

        return '';
    }
};

module.exports = utils;



