const errors = require('scripts/system/errors');
errors.registerGlobalHandler(window);

// only for ROTATED build
if (process.env.ALFA_ROTATED) {
    // enable ad rotation by default
    const settings = require('scripts/data/settings');
    settings.local.rotated_ad = true;
}

// only for VPAID build
if (process.env.ALFA_VPAID) {
    // expose 'getVPAIDAd' function to global
    window.getVPAIDAd = require('scripts/VPAIDVideoPlayer');
}

// expose player to global variable
window.WebAlfaPlayer = require('scripts/WebAlfaPlayer');



