const settings = require('../data/settings');
const utils = require('../utils/utils');

const requests = {};

class RequestError extends Error {
    constructor(xhr) {
        super();

        this.status = xhr.status;
        if (this.status) {
            this.message = xhr.statusText;
        } else {
            this.message = 'Request aborted';
        }
    }
}

class Request {
    constructor() {
        this.xhr = null;

        this.active = false;
        this.success = false;
        this.error = false;
    }

    send(url, options, callback) {
        // defaulting params
        options = options || {};
        callback = callback || utils.noop;
        const method = options.method || 'GET';

        // if request was active - abort it
        this.abort();

        // reset status flags to working status
        this.xhr = new XMLHttpRequest();
        this.active = true;
        this.success = false;
        this.error = false;

        // setup actual request
        this.xhr.open(method, url, true);

        // if request timeout was set - setup it
        this.xhr.timeout = options.timeout || 0;

        this.xhr.onload = () => {
            this.active = false;
            this.success = true;

            callback(null, this.xhr.responseText);
        };

        this.xhr.onerror = () => {
            this.active = false;
            this.error = true;

            console.error('[alfa] Error in request to: ' + url);
            callback(new RequestError(this.xhr));
        };

        this.xhr.ontimeout = () => {
            this.active = false;
            this.error = true;

            console.error('[alfa] Timeout in request to: ' + url);
            callback(new RequestError(this.xhr));
        };

        // setup request headers
        if (options.headers) {
            for (let headerName in options.headers) {
                if (options.headers.hasOwnProperty(headerName)) {
                    this.xhr.setRequestHeader(headerName, options.headers[headerName]);
                }
            }
        }

        // actually start request
        try {
            if (options.body != null) {
                this.xhr.send(options.body);
            } else {
                this.xhr.send();
            }
        } catch (e) {
            callback(e);
        }
    }

    post(url, options, callback) {
        options.method = 'POST';
        this.send(url, options, callback);
    }

    abort() {
        if (this.xhr && this.active) {
            this.active = false;
            this.xhr.abort();
        }
    }
}

module.exports = {
    /**
     * Run a GET request
     *
     * @param url
     * @param [options]
     * @param [callback]
     */
    doGet(url, options, callback) {
        options = options || {};
        callback = callback || utils.noop;

        const req = this.getRequest();
        req.send(url, options, callback);
    },

    /**
     * Run a POST request
     *
     * @param url
     * @param [options]
     * @param [callback]
     */
    doPost(url, options, callback) {
        options = options || {};
        callback = callback || utils.noop;

        const req = this.getRequest();
        req.post(url, options, callback);
    },

    /**
     *
     * @param [name]
     * @returns {Request}
     */
    getRequest(name) {
        if (!name) {
            return new Request();
        }

        let req = requests[name];
        if (!req) {
            req = new Request();
            requests[name] = req;
        }
        return req;
    },

    getShortURL(queries, callback) {
        const REQUEST_TIMEOUT = 3000;
        callback = callback || utils.noop;

        const tinyUrl = '//' + settings.tiny_url_host + '/generate';
        const reqUrl = tinyUrl + '?' + queries;

        const req = this.getRequest('shareUrl');
        req.send(reqUrl, {
            timeout: REQUEST_TIMEOUT
        }, (error, result) => {
            if (error) {
                return callback();
            }
            callback(result);
        });
    }
};



