module.exports = {
    prepareFetchVastUrl(url, rect) {
        if (url.indexOf('player_width=') > 0) {
            url = url.replace(/player_width=\d.*?(?=&|$)/, 'player_width=' + rect.width);
            url = url.replace(/player_height=\d.*?(?=&|$)/, 'player_height=' + rect.height);
        } else {
            url += '&player_width=' + rect.width + '&player_height=' + rect.height;
        }

        return url;
    },

    checkHttpsAwareness(url) {
        const HTTPS_AD_DOMAINS = '' +
            'ad.doubleclick.net|' +
            'bid.g.doubleclick.net|' +
            'corp.google.com|' +
            'ggpht.com|' +
            'google.co.uk|' +
            'google.com|' +
            'googleads.g.doubleclick.net|' +
            'googleads4.g.doubleclick.net|' +
            'googleadservices.com|' +
            'googlesyndication.com|' +
            'googleusercontent.com|' +
            'gstatic.com|' +
            'prod.google.com|' +
            'pubads.g.doubleclick.net|' +
            's0.2mdn.net|' +
            'static.doubleclick.net|' +
            'surveys.g.doubleclick.net|' +
            'youtube.com|' +
            'ytimg.com';
        const httpsRegex = new RegExp('^https?://([a-z0-9-]{1,63}\\.)*(' + HTTPS_AD_DOMAINS + ')(:[0-9]+)?([/?#]|$)', 'i');

        if ('https:' === location.protocol && httpsRegex.test(url))
            return url.replace(/^http:/i, 'https:');
        return url;
    },

    parseYoutubeID(url) {
        if (!url || typeof url !== 'string') {
            return null;
        }

        const regExp = /^.*youtu\.?be\/?(\.com\/watch\?.*&?\??v=)?([^.\\#&?]*).*/;
        const match = url.match(regExp);
        if (match && match[2].length >= 11) {
            return match[2];
        }

        const embedRegExp = /youtube\.com\/embed\/([^#&?]+)/;
        const embedMatch = url.match(embedRegExp);
        if (embedMatch && embedMatch[1].length >= 11) {
            return embedMatch[1];
        }

        return null;
    }
};



