const Vast = require('./data').Vast;
const AdTag = require('./data').AdTag;

class ParserVast1 {
    constructor(version) {
        this.vastInfo = new Vast(version);
        this.wrapperCounter = 0;
    }

    parse(xmlDoc, callback) {
        const ad = xmlDoc.getElementsByTagName('Ad')[0];
        try {
            let video = ad.getElementsByTagName('Video')[0];
            let mediaFile = video.getElementsByTagName('MediaFile');
            this.vastInfo.videoAd = mediaFile[0].getElementsByTagName('URL')[0].firstChild.data;
            this.vastInfo.videoClickThrough = video.getElementsByTagName('VideoClicks')[0].getElementsByTagName('ClickThrough')[0].getElementsByTagName('URL')[0].firstChild.data;
        } catch (e) {
            console.error('[alfa] Error parsing vast version 1 (video section)', e);
        }

        const adTag = new AdTag();
        this.vastInfo.adTags.push(adTag);

        const urls = ad.getElementsByTagName('Impression')[0].getElementsByTagName('URL');
        for (let i = 0; i < urls.length; i++) {
            adTag.impressions.push(urls[i].firstChild.data);
        }

        const tracking = ad.getElementsByTagName('TrackingEvents')[0].getElementsByTagName('Tracking');
        for (let i = 0; i < tracking.length; i++) {
            let eventName = tracking[i].getAttribute('event');
            if (!eventName)
                continue;

            const urls = tracking[i].getElementsByTagName('URL');
            adTag.events[eventName.toLowerCase()] = [];
            for (let j = 0; j < urls.length; j++) {
                if (urls[j].firstChild.data)
                    adTag.events[eventName.toLowerCase()].push(urls[j].firstChild.data);
            }
        }

        callback(null, this.vastInfo);
    }
}

module.exports = ParserVast1;



