module.exports = {
    isChildOf(c, p) {
        if (c) {
            while ((c = c.parentNode) && c !== p) ;
        }
        return !!c;
    },

    isElementInViewport(el) {
        let rect = el.getBoundingClientRect();
        return (
            rect.top >= 0 &&
            rect.left >= 0 &&
            rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            rect.right <= (window.innerWidth || document.documentElement.clientWidth)
        );
    },

    isElementPartialyInViewport(el) {
        let rect = el.getBoundingClientRect();
        let heightPart = rect.height / 5;
        let widthPart = rect.width / 5;
        let viewHeight = (window.innerHeight || document.documentElement.clientHeight);
        let viewWidth = (window.innerWidth || document.documentElement.clientWidth);
        return (
            (rect.bottom >= heightPart && rect.right >= widthPart && (rect.top + heightPart) < viewHeight) ||
            (rect.bottom >= heightPart && (rect.left + widthPart) < viewWidth && (rect.top + heightPart) < viewHeight) ||
            ((rect.top + heightPart) < viewHeight && rect.right >= widthPart && rect.bottom > heightPart) ||
            ((rect.top + heightPart) < viewHeight && (rect.left + widthPart) < viewWidth && rect.bottom > heightPart)
        );
    },

    getCursorPosition(canvas, event) {
        let rect = canvas.getBoundingClientRect();
        let xx = event.clientX - rect.left;
        let yy = event.clientY - rect.top;

        return {x: xx, y: yy};
    },

    normalizePercent(percent) {
        if (percent < 0)
            percent = 0;
        else if (percent > 1)
            percent = 1;
        return percent;
    },

    showElementFullsceen(elem) {
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.msRequestFullScreen) {
            elem.msRequestFullScreen();
        } else if (elem.mozRequestFullScreen) {
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) {
            elem.webkitRequestFullscreen();
        } else if (elem.webkitEnterFullscreen) {
            elem.webkitEnterFullscreen();
        }
    },

    cancelFullScreen() {
        if (document.cancelFullScreen) {
            document.cancelFullScreen();
        } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
            document.webkitCancelFullScreen();
        }
    },

    isFullScreen() {
        return document.fullscreen
            || document.fullscreenElement
            || document.webkitIsFullScreen
            || document.webkitFullscreenElement
            || document.mozFullScreen
            || document.mozFullScreenElement
            || document.msFullscreenElement;
    }
};



