class SocialNetwork {
    constructor(name, rei, url) {
        this.name = name;
        this.rei = rei;
        this.url = url;
    }

    shareUrl(playerUrl, image, title, description) {
        if (this.name === 'facebook')
            playerUrl = playerUrl.replace('https:', 'http:');

        return this.url.replace('_URL_', playerUrl)
            .replace('_IMAGE_', image)
            .replace('_TITLE_', title)
            .replace('_DESCR_', description);
    }
}

function defineNetwork(name, rei, url) {
    return new SocialNetwork(name, rei, url);
}

module.exports = [
    defineNetwork(
        'facebook',
        'www.facebook.com',
        'https://www.facebook.com/sharer.php?u=_URL_&display=page'
    ),
    defineNetwork(
        'gplus',
        'plus.google.com',
        'https://plus.google.com/share?url=_URL_'
    ),
    defineNetwork(
        'twitter',
        'twitter.com',
        'https://twitter.com/share?url=_URL_'
    )
];



