const VPAIDFLASHClient = require('vendor/vpaid-flash-client/js/VPAIDFLASHClient');

const EventSystem = require('../system/eventSystem');
const settings = require('../data/settings');
const ContextMenu = require('../ui/contextMenu');
const constants = require('../data/constants');
const errors = require('../system/errors');

const FlashClientOpts = {data: constants.URL.VPAID_FLASH};
const VAST_EVENTS = constants.VAST_EVENTS;
const PLAYER_EVENTS = constants.PLAYER_EVENTS;
const MEDIA_EVENTS = constants.MEDIA_EVENTS;
const EVENTS = [
    'AdLoaded',
    'AdStarted',
    'AdStopped',
    'AdSkipped',
    'AdSkippableStateChange', // VPAID 2.0 new event
    'AdSizeChange', // VPAID 2.0 new event
    'AdLinearChange',
    'AdDurationChange', // VPAID 2.0 new event
    'AdExpandedChange',
    'AdRemainingTimeChange', // [Deprecated in 2.0] but will be still fired for backwards compatibility
    'AdVolumeChange',
    'AdImpression',
    'AdVideoStart',
    'AdVideoFirstQuartile',
    'AdVideoMidpoint',
    'AdVideoThirdQuartile',
    'AdVideoComplete',
    'AdClickThru',
    'AdInteraction', // VPAID 2.0 new event
    'AdUserAcceptInvitation',
    'AdUserMinimize',
    'AdUserClose',
    'AdPaused',
    'AdPlaying',
    'AdLog',
    'AdError'
];

class VPAIDPlayer extends EventSystem {
    constructor(parent, videoEl, wap) {
        super();

        this._src = '';
        this._videoEl = videoEl;
        this._paused = true;
        this._duration = 0;
        this._currentTime = 0;
        this._id = Math.floor(Math.random() * 1000);
        this._parent = parent;
        this._volume = videoEl.volume;
        this._wap = wap;
        this._type = constants.VIDEO_TYPE.HTML5;
        this._errorSent = false;
        this._frameContainer = null;

        this.style = {
            set visibility(value) {
                //self._overflow.style.visibility = value;
            },
            set display(value) {
                //self._overflow.style.display = value;
            }
        };

        this.buffered = {
            get length() {
                return 0;
            }
        };
    }

    createElement() {
        console.log('[alfa] Create VPAID container elememnt ', this._type);

        this._errorSent = false;
        this._adLoadTimeoutTimer = setTimeout(VPAIDPlayer.prototype._onAdLoadTimeout.bind(this), settings.local.vpaid_load_timeout);

        let frame;
        if (this._type === constants.VIDEO_TYPE.HTML5) {
            frame = this.createHTML5Frame();
        } else if (this._type === constants.VIDEO_TYPE.FLASH) {
            frame = this.createFlashFrame();
        }

        if (frame) {
            frame.style.border = '0px';
            frame.style.width = '100%';
            frame.style.height = '100%';
            frame.style.position = 'absolute';
            frame.style.left = '0';
            frame.style.top = '0';
            frame.style.overflow = 'hidden';
            frame.scrolling = 'no';
        }
    }

    createHTML5Frame() {
        const frame = this._frame = document.createElement('iframe');
        frame.src = 'javascript:false';

        this._parent.appendChild(frame);
        let d = frame.contentWindow.document;
        d.open().write(this.generateHTML5FrameSource(this._src));
        frame.contentWindow.alfaLoad = this.onIFrameReady.bind(this);
        frame.contentWindow.alfaError = (error) => this._emitError(error);
        d.close();

        frame.contentWindow.document.addEventListener('contextmenu', (e) => {
            console.log('ASD CONTEXT MENUS');
            let items = [
                {id: 'about_player', text: 'About Web Alfa player', href: 'http://www.alfaplayer.com/share/'}
            ];
            ContextMenu.showAt(e.x, e.y, items, this._parent);
            e.preventDefault();
        });

        //this._videoEl.addEventListener('timeupdate', this.onHTML5TimeUpdate.bind(this));
        //this._videoEl.addEventListener('durationchange', this.onHTML5DurationChange.bind(this));

        return frame;
    }

    generateHTML5FrameSource(src) {
        return '<head>' +
            '<script>' +
            'function onLoad() {' +
            '    var js = document.createElement("script");' +
            '    js.src = "' + src + '";' +
            '    js.addEventListener("load", function() { window.alfaLoad(); });' +
            '    js.addEventListener("error", function(error) { window.alfaError(new URIError("VPAID script " + error.target.src + " is not accessible.")); });' +
            '    document.body.appendChild(js);' +
            '}' +
            '</script>' +
            '</head>' +
            '<body onload="onLoad()" style="margin: 0">';
    }

    createFlashFrame() {
        let frame;

        if (this._frameContainer === null) {
            frame = this._frameContainer = document.createElement('span');
            this._frameContainer.className = 'alfa-vpaid-container';
            //this._frameContainer.style.display = 'none';
            this._parent.appendChild(this._frameContainer);
        }

        const vpaidFlashClient = new VPAIDFLASHClient(this._frameContainer, function (error) {
            if (error) {
                console.log('[alfa] VPAID Error:', error);
                this._emitError(error);
            }

            vpaidFlashClient.loadAdUnit(this._src, function (error, adUnit) {
                if (error) {
                    console.log('[alfa] VPAID Error:', error);
                    this._emitError(error);
                }

                this._creative = adUnit;
                let adParams = this._wap.currentAd.adParameters != null ? this._wap.currentAd.adParameters : '';
                let adUnitData = {AdParameters: adParams};
                let rect = this._parent.getBoundingClientRect();
                adUnit.initAd(rect.width, rect.height, 'normal', -1, adUnitData);

                EVENTS.forEach((eventName) => {
                    if (typeof(this['on' + eventName]) === 'function')
                        adUnit.on(eventName, this['on' + eventName].bind(this));
                });

            }.bind(this));
        }.bind(this), FlashClientOpts);

        return frame;
    }

    play() {
        if (this._creative) {
            this._creative.resumeAd();
            this._paused = false;
            this.emit(MEDIA_EVENTS.play);
        }
    }

    pause() {
        if (this._creative) {
            console.log('pause VPAID ad');
            this._creative.pauseAd();
        }
    }

    skipAd() {
        console.log('skip VPAID ad');
        if (this._creative)
            this._creative.skipAd();
    }

    resizeAd(width, height, viewMode) {
        console.log('resize VPAID ad');
        if (this._creative)
            this._creative.resizeAd(width, height, viewMode);
    }

    expandAd() {
        if (this._creative)
            this._creative.expandAd();
    }

    collapseAd() {
        if (this._creative)
            this._creative.collapseAd();
    }

    getAdLinear() {
        if (this._creative)
            return this._creative.getAdLinear();
        else
            return true;
    }

    getAdExpanded() {
        if (this._creative)
            return this._creative.getAdExpanded();
        else
            return false;
    }

    getAdSkippableState() {
        if (this._creative)
            return this._creative.getAdSkippableState();
        else
            return false;
    }

    get paused() {
        return this._paused;
    }

    get type() {
        return this._type;
    }

    set type(value) {
        this._type = value;
    }


    get volume() {
        return this._volume;
    }

    set volume(value) {
        this._volume = value;
        try {
            if (this._creative)
                this._creative.setAdVolume(value);
        } catch (e) {
            //console.error('VPAID error',e.message);
        }
    }

    get error() {
        return null;
    }

    get duration() {
        return this._duration;
    }

    get currentTime() {
        return this._currentTime;
    }

    //not implemented
    get src() {
        return this._src;
    }

    set src(value) {
        this._src = value;
        this.createElement();
    }


    onAdImpression() {
        console.log('VPAID AdImpression');
        this._wap.notifyImpressions();
    }


    onAdVideoFirstQuartile() {
        console.log('VPAID onAdVideoFirstQuartile');
        this._wap.notifyOnce(VAST_EVENTS.FIRST_QUARTILE);
    }


    onAdVideoMidpoint() {
        console.log('VPAID onAdVideoMidpoint');
        this._wap.notifyOnce(VAST_EVENTS.MIDPOINT);
    }

    onAdVideoThirdQuartile() {
        console.log('VPAID onAdVideoThirdQuartile');
        this._wap.notifyOnce(VAST_EVENTS.THIRD_QUARTILE);
    }

    onAdLoaded() {
        this._creative.startAd();
        console.log('VPAID on adLOADED');

        if (this._adLoadTimeoutTimer)
            clearTimeout(this._adLoadTimeoutTimer);
        this._duration = 0;
        this._currentTime = 0;
        this._paused = false;
        this.emit(MEDIA_EVENTS.play);

        if (this._type === constants.VIDEO_TYPE.FLASH) {
            this._creative.getAdRemainingTime(function (err, value) {
                if (err == null) {
                    this._duration = value;
                    this.emit(MEDIA_EVENTS.durationchange);
                }
            }.bind(this));
        }
        else if (this._type === constants.VIDEO_TYPE.HTML5) {
            //this._duration = this._creative.getAdDuration();
        }

        try {
            //this.emit(PLAYER_EVENTS.TIME_UPDATE);

            this._creative.setAdVolume(this._volume);
            //this._wap.emit(PLAYER_EVENTS.VOLUME_CHANGED, this._volume);
        } catch (e) {
            //console.error('VPAID error',e.message);
        }
    }

    onAdVideoStart() {
        console.log('VPAID on ad stared event!!!');
        this._paused = false;
        this._wap.notifyOnce(VAST_EVENTS.START);
    }

    onAdClickThru(url, id, playerHandles) {
        console.log('VPAID on ad clicked');
        this.emit(PLAYER_EVENTS.AD_CLICK_THROUGH, url, id, playerHandles);
    }

    onAdExpandedChange() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdExpandedChange');
    }

    onAdInteraction() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdInteraction');
    }

    onAdUserAcceptInvitation() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserAcceptInvitation');
    }

    onAdUserMinimize() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserMinimize');
    }

    onAdUserClose() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserClose');
    }

    onAdSkippableStateChange() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdSkippableStateChange');
    }

    onAdVideoComplete() {
        console.log('VPAID onAdVideoComplete');
        this._wap.notifyOnce(VAST_EVENTS.COMPLETE);
    }

    onAdPaused() {
        console.log('VPAID on paused');
        this._paused = true;
        this.emit(MEDIA_EVENTS.pause);
    }

    onAdPlaying() {
        console.log('VPAID on playing');
        this._paused = false;
        this.emit(MEDIA_EVENTS.play);
        this.emit(MEDIA_EVENTS.timeupdate);
    }

    onAdError(e) {
        console.log('VPAID onAdError:', e);
        this._paused = true;
        if (this._type === constants.VIDEO_TYPE.FLASH) {
            //var node = this._frameContainer;
            // while (node.firstChild) {
            //     node.removeChild(node.firstChild);
            // }
            this._parent.removeChild(this._frameContainer);
            this._frameContainer = null;
        } else {
            this._parent.removeChild(this._frame);
        }
        this._frame = void(0);
        this._creative = void(0);
        if (this._adLoadTimeoutTimer)
            clearTimeout(this._adLoadTimeoutTimer);
        if (!this._errorSent)
            this._errorSent = true;

        const vastError = errors.createVastError(errors.VAST.VPAID_ERROR, e && e.message || e);
        this.emit(MEDIA_EVENTS.error, vastError);
    }

    onAdDurationChange() {
        /*
         if(this._type === constants.VIDEO_TYPE.FLASH) {
         this._creative.getAdRemainingTime(function(err, value){
         if(err == null) {
         this._duration = value;
         this.emit(PLAYER_EVENTS.DURATION_CHANGE);
         }
         }.bind(this));
         }
         else */
        if (this._type === constants.VIDEO_TYPE.HTML5 && this._creative) {
            this._duration = this._creative.getAdDuration();
            this._currentTime = this._duration - this._creative.getAdRemainingTime();
            this.emit(MEDIA_EVENTS.timeupdate);
            this.emit(MEDIA_EVENTS.durationchange);
        }
    }

    onAdVolumeChange() {
        let vol = this._creative.getAdVolume();
        this._wap.ui.setVolumeUI(vol);
        this._wap.emit(MEDIA_EVENTS.volumechange, vol);
    }

    onAdRemainingTimeChange() {
        //console.log('VPAID onAdRemainingTimeChange');
        if (this._type === constants.VIDEO_TYPE.FLASH) {
            this._creative.getAdRemainingTime(function (err, value) {
                if (err == null) {
                    this._onRemainingTimeChange(value);
                }
            }.bind(this));
        }
        else if (this._type === constants.VIDEO_TYPE.HTML5 && this._creative) {
            this._duration = this._creative.getAdDuration();
            let value = this._creative.getAdRemainingTime();
            this._onRemainingTimeChange(value);
        }
    }

    onAdStopped() {
        console.log('VPAID AdStopped');

        if (this._type === constants.VIDEO_TYPE.FLASH) {
            // while(this._frameContainer.firstChild)
            //     this._frameContainer.removeChild(this._frameContainer.firstChild);
            this._parent.removeChild(this._frameContainer);
            this._frameContainer = null;
        } else {
            if (this._frame.parentNode)
                this._frame.parentNode.removeChild(this._frame);
        }

        setTimeout(function () {
            this.emit(MEDIA_EVENTS.ended);
        }.bind(this), 75);
        this._frame = void(0);
        this._creative = void(0);
    }

    onAdSkipped() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'onAdSkipped');
    }

    _emitError(error) {
        if (!this._errorSent) {
            this._errorSent = true;

            const vastError = errors.createVastError(errors.VAST.VPAID_ERROR, error && error.message || error);
            this.emit(MEDIA_EVENTS.error, vastError);
        }
    }

    _onAdLoadTimeout() {
        console.log('adloadtimeout');
        this._emitError('adloadtimeout');
    }

    _onRemainingTimeChange(value) {
        if (this._duration === 0) {
            this._duration = value;
            this.emit(MEDIA_EVENTS.durationchange);
        }
        this._currentTime = this._duration - value;
        this.emit(MEDIA_EVENTS.timeupdate);
    }

    onHTML5TimeUpdate() {
        if (this._type !== constants.VIDEO_TYPE.HTML5)
            return;
        console.log('vpaid html5 timeupdate');
    }

    onHTML5DurationChange() {
        if (this._type !== constants.VIDEO_TYPE.HTML5)
            return;
        console.log('vpaid html5 duration change');
    }

    onParentResize() {
        console.log('OnParentResize');
        if (this._creative) {
            let rect = this._videoEl.getBoundingClientRect();
            this._creative.resizeAd(rect.width, rect.height, 'normal');
        }
    }

    onIFrameReady() {
        console.log('VPAID IFRAME ready');
        let vpaidAD = this._creative = this._frame.contentWindow.getVPAIDAd();
        let adEl = this._frame.contentWindow.document.createElement('div');
        adEl.className = 'ad-element';
        adEl.style.height = '100%';
        this._frame.contentWindow.document.body.appendChild(adEl);
        let rect = this._videoEl.getBoundingClientRect();
        let adParams = this._wap.currentAd.adParameters != null ? this._wap.currentAd.adParameters : '';

        EVENTS.forEach(function (eventName) {
            if (typeof(this['on' + eventName]) === 'function')
            //console.log('vpaid subscribe',eventName);
                vpaidAD.subscribe(
                    this['on' + eventName],
                    eventName,
                    this);
        }.bind(this));

        vpaidAD.initAd(rect.width, rect.height, 'normal', 1000,
            {
                AdParameters: adParams
            }
            , {
                slot: adEl,
                videoSlot: this._videoEl
            });
    }
}

module.exports = VPAIDPlayer;



