function Vast(version) {
    this.version = version || 0;
    this.adTags = [];
}

function AdTag() {
    this.videoClickThrough = null;
    this.videoClickTracking = [];
    this.impressions = [];
    this.videoAd = null;
    this.skipOffset = 0;
    this.events = {};
    this.companions = [];
    this.nonLinearAds = null;
}

function Companion() {
    this.width = 0;
    this.height = 0;
    this.staticResource = null;
    this.htmlResource = null;
    this.iframeResource = null;
    this.clickThrough = null;
    this.events = {};
}

function InnerNonLinear() {
    this.staticResource = null;
    this.htmlResource = null;
    this.iframeResource = null;
    this.clickThrough = null;
}

function NonLinear() {
    this.events = {};
    this.nonLinears = [];
}

module.exports = {
    Vast,
    AdTag,
    Companion,
    InnerNonLinear,
    NonLinear
};



